/* @import-normalize; */
 /* bring in normalize.css styles */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
}

.header__wrapper{
  background-color: #f44336;
    padding: 30px 40px;
    color: white;
    text-align: center;
}
.title{
  font-size: 30px;
  text-transform: uppercase;
  color: #fdf7f7;

}

.input {
  margin: 0 auto;
    border: none;
    border-radius: 0;
    width: 50%;
    padding: 10px;
    font-size: 16px;
}

.addBtn{
  padding: 10px;
    width: 10%;
    background: #d9d9d9;
    color: #555;
    text-align: center;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
    border-radius: 0;
    border: none;
}

.addBtn:hover {
  background-color: #bbb;
}

/* .list:nth-child(odd){
background: #f9f9f9;
} */

.list{
    /* position: relative; */

    padding: 12px 8px 12px 40px;
    list-style-type: none;
    background: #eee;
    font-size: 18px;
    transition: 0.2s;
}

.list__item{
      position: relative;

  background: #eee;
    font-size: 18px;
    transition: 0.2s;
      cursor: pointer;
  
}

.list__item:hover{
  background-color: #ddd;
}

.list__item:nth-child(odd) {
  background: #f9f9f9;
}

.list__item.checked {
  background: #888;
  color: #fff;
  text-decoration: line-through;
}

.list__item.checked::before {
    position: absolute;

  top: 0px;
  left: -20px;
  transform: rotate(45deg);

  content: '';
  border-color: #fff;
  border-style: solid;
  border-width: 0 2px 2px 0;

 
  height: 15px;
  width: 7px;
}